import {
    debounce
} from "./utils"
import backTop from "components/content/backtop/BackTop";


export const imgloadMix = {
    data() {
        return {
            imgListener: null,
            newRefresh: null
        }
    },
    methods: {

    },
    mounted() {
        // console.log("this is mixsin");
        // 定义一个变量来存储这个方法，可以在离开当前也面时取消全局事件的监听
        this.newRefresh = debounce(this.$refs.scrolltop.refresh, 100);
        this.imgListener = () => {
            this.newRefresh()
        }
        this.$bus.$on("itemimageLoad", this.imgListener);
    }
}

export const backTopMix = {
    components: {
        backTop,
    },
    data() {
        return {
            isShowBackTop: false,
        }
    },
    methods: {
        backClick() {
            // console.log(1)
            this.$refs.scrolltop.ScrollTo(0, 0, 500);
          },
    }
}